<template>
  <div>
    <el-tabs v-model="activeName" @tab-click="handleClick" class="platform">
      <el-tab-pane label="菜单管理" name="first">
        <div>
          <div class="bg">
            <div>
              <el-button class="input" @click="openDialog">添加菜单</el-button>
            </div>
            <DxDataGrid :data-source="datalist" :allow-column-resizing="true" :show-column-lines="false"
              :auto-expand-all="false" :show-row-lines="true" :column-auto-width="true" :focused-row-enabled="true"
              :auto-navigate-to-focused-row="true" width="98%" key-expr="key" class="main_table"
              style="height: calc(248px  + 42vh);width:98%">
              <DxColumn data-field="id" alignment="center" caption="ID" />
              <DxColumn data-field="name" alignment="center" caption="菜单名称" />
              <DxColumn data-field="code" alignment="center" caption="权限编码" />
              <DxColumn caption="操作" alignment="center" cell-template="caozuo" />
              <template :width="280" #caozuo="{ data: rowInfo }">
                <div class="caozuo_btn">
                  <span @click="updatalist(rowInfo)" class="el-icon-edit caozuo_edit" title="修改"></span>
                  <span @click="getcodelist(rowInfo)" class="el-icon-setting caozuo_edit" title="编辑"></span>
                  <span @click="deletistinfo(rowInfo)" class="el-icon-delete caozuo_edit" title="删除"></span>
                </div>
              </template>
              <DxScrolling use-native="false" />
            </DxDataGrid>
            <el-pagination :page-size="pagesize" :pager-count="5" :page-sizes="[10, 20, 30, 40]"
              @size-change="handleSizeChange" layout="total, sizes, prev, pager, next, jumper" :total="total"
              @current-change="handleCurrentChange">
            </el-pagination>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="角色管理" name="second">
        <div class="role">
          <div class="choicerole">
            <div class="topname">请选择用户角色</div>
            <div>
              <div v-for="(item, i) in roles" :key="i" class="roleitem" @click="whole(item.value)"
                :class="{ type: ys1 == item.value }"> {{ item.label }} </div>
            </div>
          </div>
          <div class="rolemesssge">
            <div class="multi-check-item" v-for="(item, i) in rolename" :key="i">
              <el-checkbox v-model="item.checked" :label="item.name" :value="item.id" :checked="item.checked">
                <span>{{ item.name }}</span>
                <span style="position: absolute;left: 700px;">菜单栏 </span>
              </el-checkbox>
              <!-- <div class="multi-check-item" style="display: flex;flex-direction: column"> -->
              <div class="multi-check-item1">
                <el-checkbox v-model="child.checked" v-for="(child, i1) in item.node || []" :key="i1"
                  :label="child.name" :value="item.id" :checked="child.checked"> {{ child.name }} </el-checkbox>
              </div>
            </div>
            <el-button @click="savechoice" class="msgsave">保存权限</el-button>
          </div>
        </div>
        <el-dialog title="信息" :visible.sync="suredeletistinfomsg" width="30%" :before-close="handleToggleDialog">
          <div class="dialog_form main_form">
            <span>添加成功</span>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="suredeletistinfomsg = false">确 定</el-button>
          </span>
        </el-dialog>
      </el-tab-pane>
    </el-tabs>
    <el-dialog title="添加/修改" :visible.sync="updatalistinfo" width="70%" :before-close="handleToggleDialog">
      <div class="dialog_form main_form">
        <el-form ref="nodeinfoForm" :model="nodeinfoForm" :rules="rules" size="medium" label-width="180px">
          <el-form-item label="菜单名称" prop="name">
            <el-input v-model="nodeinfoForm.name" placeholder="请输入菜单名称" clearable :style="{ width: '100%' }">
            </el-input>
          </el-form-item>
          <el-form-item label="权限编码" prop="code">
            <el-input v-model="nodeinfoForm.code" placeholder="请输入权限编码" clearable :style="{ width: '100%' }">
            </el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="updatalistinfo = false">取 消</el-button>
        <el-button type="primary" @click="handleSubmit('form')">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="删除" :visible.sync="deletistinfomsg" width="30%" :before-close="handleToggleDialog">
      <div class="dialog_form main_form">
        <span>是否删除{{ nodeinfoForm.name }}的数据记录？</span>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deletistinfomsg = false">取 消</el-button>
        <el-button type="primary" @click="Submitdeletistinfo('form')">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="编辑控制页功能" :visible.sync="getcodeinfodialog" width="70%" :before-close="handleToggleDialog">
      <div class="dialog_form main_form">
        <span>请为菜单项{{ nodeinfoForm.name }}设置权限控制信息</span>
        <el-form ref="codeForm" :model="codeForm" :rules="rules" size="medium" label-width="180px">
          <el-row>
            <el-col :span="8">
              <el-form-item label="功能项名称" prop="name">
                <el-input v-model="codeForm.name" placeholder="请输入功能项名称" clearable :style="{ width: '100%' }">
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="权限编码" prop="code">
                <el-input v-model="codeForm.code" placeholder="请输入权限编码" clearable :style="{ width: '100%' }">
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="3">
              <el-button class="save" @click="codesurelist"> 保 存</el-button>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <DxDataGrid :data-source="twodatalist" :allow-column-resizing="true" :show-column-lines="false"
        :auto-expand-all="false" :show-row-lines="true" :column-auto-width="true" :focused-row-enabled="true"
        :auto-navigate-to-focused-row="true" width="100%" key-expr="key" class="main_table"
        style="height: calc(42vh);width:100%">
        <DxColumn data-field="id" alignment="center" caption="ID" />
        <DxColumn data-field="name" alignment="center" caption="功能项名称" />
        <DxColumn data-field="code" alignment="center" caption="权限编码" />
        <DxScrolling use-native="false" />
      </DxDataGrid>
      <span slot="footer" class="dialog-footer">
        <el-button @click="getcodeinfodialog = false">取 消</el-button>
        <el-button type="primary" @click="codeinfohandleSubmit('form')">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import platform from "./platform";
export default platform;
</script>
<style scoped>
@import url("./platform.css");
@import url("../../assets/css/houtaicommon.css");
</style>



