import $ from 'jquery';
// import "@/libs/ztree/js/jquery.ztree.core.js"
import "ztree/js/jquery.ztree.core.min.js";
import "ztree/js/jquery.ztree.excheck.min.js";
import "ztree/css/metroStyle/metroStyle.css";
import {
  getGlobalCode,
  saveGlobalcode,
  get_menu_tree,
  saveRoleFunc,
  deleteGlobalcode
} from '../../utils/service';
import {
  DxSelection,
  DxDataGrid,
  DxColumn,
  DxFilterRow,
  DxPager,
  DxFilterPanel,
  DxPaging,
  DxHeaderFilter,
  DxScrolling
} from "devextreme-vue/data-grid";
import zhMessages from "devextreme/localization/messages/zh.json";
import {
  locale,
  loadMessages
} from "devextreme/localization";
export default {
  components: {
    DxSelection,
    DxDataGrid,
    DxColumn,
    DxFilterRow,
    DxPager,
    DxFilterPanel,
    DxPaging,
    DxHeaderFilter,
    DxScrolling
  },
  props: {},
  created() {
    loadMessages(zhMessages);
    locale(navigator.language);
  },
  data() {
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.nodeinfoForm.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      datalist: '',
      total: 0,
      pagesize: 20,
      pageindex: 1,
      updatalistinfo: false,
      deletistinfomsg: false,
      suredeletistinfomsg: false,
      nodeinfoForm: '',
      role: '',
      deletid: '',
      activeName: 'first',
      rules: {
        name: [{
          required: true,
          message: '请输入菜单名称',
          trigger: 'blur'
        }],
        code: [{
          required: true,
          message: '请输入权限编码',
          trigger: 'blur'
        }],
      },
      getcodeinfodialog: false,
      codeForm: {
        name: '',
        code: ''
      },
      parentcode: '',
      roles: [{
        "label": "管理员角色",
        "value": 0
      }, {
        "label": "一般用户角色",
        "value": 2
      }, {
        "label": "审计角色",
        "value": 3
      }],
      ys1: 0,
      rolename: '',
      twodatalist: '',
    };
  },
  mounted() {
    this.getSearchDatalist()
    this.chocked()
  },
  computed: {},
  methods: {
    handleClick(tab, event) {
      // console.log(tab, event);
    },
    // 初始化数据
    getSearchDatalist() {
      var formData = {
        pageindex: 1,
        pagesize: this.pagesize,
        pcode: 'sysmenu'
      }
      getGlobalCode(formData, res => {
        this.total = res.count
        this.datalist = res.data
        this.datalist.map((x, index) => {
          x.key = index + 1;
        });
      });
    },
    // 页码数改变
    handleCurrentChange(e) {
      this.pageindex = e
      var formData = {
        pageindex: e,
        pagesize: this.pagesize
      }
      search(formData, res => {
        this.total = res.count
        this.datalist = res.data
        this.datalist.map((x, index) => {
          x.key = index + 1;
        });
      });
    },
    handleSizeChange(e) {
      this.pagesize = e
    },
    cslistchange(value) {
      this.nodeinfoForm.name = this.csname
    },
    // 数据编辑时打开弹窗
    updatalist(rowInfo) {
      this.nodeinfoForm = JSON.parse(JSON.stringify(rowInfo.data))
      this.updatalistinfo = true
      this.role = this.nodeinfoForm.role
      // console.log('this.nodeinfoForm.id :>> ', this.nodeinfoForm);
    },
    // 编辑、新增后保存信息
    handleSubmit() {
      this.$refs.nodeinfoForm.validate(valid => {
        if (valid) {
          this.updatalistinfo = false
          let formData = new FormData();
          formData = {
            name: this.nodeinfoForm.name,
            code: this.nodeinfoForm.code,
            id: "0",
            parentcode: "sysmenu",
          }
          saveGlobalcode(formData, res => {

          });
          setTimeout(() => {
            this.$nextTick(() => {
              this.getSearchDatalist()
            })
          }, 200);

        } else {
          this.updatalistinfo = true
        }
      });

    },
    handleToggleDialog() {
      this.updatalistinfo = false;
    },
    //添加时重置信息
    reset() {
      this.nodeinfoForm = {
        code: "",
        name: "",
      };
    },
    //添加
    openDialog() {
      this.reset();
      this.$nextTick(() => {
        this.$refs["nodeinfoForm"].clearValidate();
      });
      this.updatalistinfo = true;
    },
    // 删除
    deletistinfo(rowInfo) {
      this.deletistinfomsg = true
      this.deletid = rowInfo.data.id
      this.nodeinfoForm = rowInfo.data
      //   console.log('this.deletid :>> ', rowInfo);
    },
    // 确认删除
    Submitdeletistinfo() {
      this.deletistinfomsg = false
      var formData = {
        tablename: "global_const_code",
        id: this.deletid
      }
      deleteGlobalcode(formData, res => {});
      this.$nextTick(() => {
        this.getSearchDatalist()
      })
      this.getSearchDatalist()

    },
    getcodelist(rowInfo) {
      console.log('rowInfo.data :>> ', rowInfo.data);
      this.parentcode = rowInfo.data.code
      this.getcodeinfodialog = true
      var code = rowInfo.data.code
      console.log('rowInfo :>> ', rowInfo);
      var formData = {
        pageindex: 1,
        pagesize: this.pagesize,
        pcode: code
      }
      getGlobalCode(formData, res => {
        // this.total = res.count
        this.twodatalist = res.data
        if (this.twodatalist) {
          this.twodatalist.map((x, index) => {
            x.key = index + 1;
          });
        }
      });
    },
    codesurelist() {
      var formData = {
        name: this.codeForm.name,
        code: this.codeForm.code,
        id: '0',
        parentcode: this.parentcode
      }
      saveGlobalcode(formData, res => {
        var formData = {
          pageindex: 1,
          pagesize: this.pagesize,
          pcode: this.parentcode
        }
        getGlobalCode(formData, res => {
          // this.total = res.count
          this.twodatalist = res.data
          if (this.twodatalist) {
            this.twodatalist.map((x, index) => {
              x.key = index + 1;
            });
          }
        });
      });
    },
    codeinfohandleSubmit() {
      this.getcodeinfodialog = false
    },
    whole(index) {
      this.ys1 = index
      this.chocked()
    },
    chocked() {
      var formData = {
        roleid: this.ys1,
      }
      get_menu_tree(formData, res => {
        this.rolename = res.data
      });
    },
    savechoice() {
      var list = ''
      this.rolename.map(item => {
        if (item.node) {
          item.node.map(i => {
            if (i.checked) {
              if (list != '') {
                list = list + ',' + i.id
              } else {
                list = i.id
              }
            }
          })
        }
        if (item.checked) {
          if (list != '') {
            list = list + ',' + item.id
          } else {
            list = item.id
          }
        }
      })
      var formData = {
        roleid: this.ys1,
        funcids: list
      }
      saveRoleFunc(formData, res => {
        this.chocked()
      });
      this.suredeletistinfomsg = true
    },
  },
};



